/* Profile Page */
.blankProfileImage{
    height: 25vh;
    border-radius: 100%;
}

.center-text {
  text-align: center;
}

.left-text {
  text-align: left
}
.left-checkbox {
  width: auto;
}

.large-emoji-icon {
  font-size: 80px;
  margin-bottom: -12px;
}
.input-box-white {
  display: block;
  font-size: 16px;
  border: none;
  padding: 10px;
  border-radius: 4px;
  margin-right: 10px;
  background-color: var(--color-gunmetal);
  color: #fff;
  width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
}

.left {
  float: left;
}

.right {
  float: right;
}

.landingLogo {
  text-align: center;
  padding-top: 100px !important;
}

#loginMain {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  text-align: left;
  padding: 0 20px;
}

#loginMain h1 {
  font-size: 2rem;
}

#signupMain{
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  text-align: left;
}

.passwordConstraint{
  font-size: 10px;
  color:#5F6A82;
}

/* Upload post */
#createPostMain{
    text-align: center;
}

#createPostHolder{
    background-color: #3D4554;
    margin:5%;
    border-radius: 1%;
    text-align: left;
    padding: 20px;
}

#NewPostImage{
  max-width: 800px;
}

#newVideoPreview{
    width: 100%;
    height: 100%;
}

.uploadLogo{
    height: 200px;
    width: 200px;
    text-align: center;
    cursor: pointer;
}

/* user posts */
#userPostsMain{
    text-align: center;
    width: 100vw;
    min-height: 100vh;
    max-height: fit-content;
}

/*posts*/
.post{
    border-radius: 1%;
    width: 60%;
    background-color: #3D4554;
    height: 300px;
    text-align: left;
    padding: 20px;
    float: right;
    margin: 5px;
    position:relative;
}

.imagePostLeft{
    width: 39%;
}

.videoPostLeft{
    width: 39%;
}

.postVideo{
    border-radius: 5%;
    height: 300px;
}

.postImage{
    border-radius: 5%;
    height: auto;
    width: fit-content;
    max-width: stretch;
    height: 300px;
}

.postCaption{
    background-color: #225578;
    width: 60%;
    border-radius: 10px;
    float: right;
    height: stretch;
}

#userPostProfile{
    float: left;
    width: 30%;
    height: fit-content;
    background-color: #3D4554;
    border-radius: 2%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
    
}

.profilePictureCircle{
    height: 50px;
    width: 50px;
    border-radius: 100%;
}

/* Home feed */
#homeFeedHeader{
    position: fixed;
    height: 80px;
    width: 100%;
    background-color: #23282E;
    top:0;
    display: flex;
    align-items: center;
    justify-content: right;
}

.homeFeedProfilePicture{
    height: 30px;
    width: 30px;
    border-radius: 100%;
}

/* Home feed */
#homeFeedHeader{
    position: fixed;
    height: 80px;
    width: 100%;
    background-color: #23282E;
    top:0;
    display: flex;
    align-items: center;
    justify-content: right;
}

#homeFeed{
    margin-top: 80px;
    display: flex;
  flex-wrap: wrap;
    justify-content: center;
}
#homeFeedMain {
  padding-bottom: 200px;
}

.homePost{
    height: fit-content;
    width: 400px;
    background-color: #5F6A82;
    border-radius: 20px;
    display: list-item;
    margin: 5px;
    text-align: left;
    padding: 10px;
    z-index: 1;
}
.homePost::marker{
  content: " "
}

.userPost{
  height: fit-content;
  width: 400px;
  background-color: #5F6A82;
  border-radius: 20px;
  display: list-item;
  margin: 5px;
  text-align: left;
  padding: 10px;
  z-index: 1;
}::marker{
  content: " "
}

.userPostDiv {
  margin-bottom: 80px;
}

.homePostImage{
  height: 300px;
  max-width: fit-content;
  margin: 10px auto;
  border-radius: 20px;
  text-align: left;
  display: flex;
  align-items: center;
}

.homeFeedProfilePicture{
    height: 30px;
    width: 30px;
    border-radius: 100%;
}

.HomePostVideo{
    height: 300px;
    width: stretch;
    margin: 10px;
    border-radius: 20px;
    text-align: left;
}

.edit_post{
    display:block;
    float:right;
    margin-top: 0.5em;
}
.delete_account{
  color: red;
  display:block;
  float:right;
  margin-top: 0.5em;
  margin-right: 20px;
}

.delete_post_desktop{
    cursor:pointer;
    float:right;
}
.delete_post_desktop:hover{
  color:red;
  background-color: #f5edec;
  border-radius: 50%;
}

.block_small {
  background-color: red;
  border: none;
  color: white;
  padding: 8px 14px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 12px;
  margin: 4px 2px;
  border-radius: 50px;
  transition-duration: 0.4s;
  cursor: pointer;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
  vertical-align: middle;
  margin-right: 15px;
  width: auto;
}
.submit_small {
  background-color: var(--color-honolulu-blue);
  color: #fff;
  padding: 10px 20px;
  border-radius: 4px;
  border: none;
  font-size: 16px;
  cursor: pointer;
  margin-top: 20px;
  width: auto;
}
